<template>
  <v-container>
    <v-row>
      <v-col :xs="8" :md="6" :sm="6">
        <v-simple-table dark>
          <template v-slot:default>
            <thead>
            <tr>
              <th style="text-align: center" @click="sortByString">
                <v-btn text>
                  <v-icon>{{ stringsArrow }}</v-icon>
                </v-btn>
                Strings
              </th>
              <th @click="sortByNumber">
                <v-btn text>
                  <v-icon>{{ numbersArrow }}</v-icon>
                </v-btn>
                Number
              </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in items" :key="item.number">
              <td>{{ item.string }}</td>
              <td>{{ item.number }}</td>
            </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'App',
  data: () => {
    return {
      stringsArrow: 'mdi-arrow-up',
      numbersArrow: 'mdi-arrow-up',
      descNumbers: false,
      descStrings: false,

      items: [
        {string: 'Mercury', number: 1},
        {string: 'Venus', number: 2},
        {string: 'Earth', number: 3},
        {string: 'Mars', number: 4},
        {string: 'Jupiter', number: 5},
        {string: 'Saturn', number: 6},
        {string: 'Uranus', number: 7},
        {string: 'Neptune', number: 8},
        {string: 'Pluto', number: 9},
      ]
    }
  },
  methods: {
    sortByNumber() {
      if (this.descNumbers) {
        this.descNumbers = false;
        this.numbersArrow = 'mdi-arrow-down';
        this.items.sort((a, b) => {
          return a.number - b.number;
        })
      } else {
        this.descNumbers = true;
        this.numbersArrow = 'mdi-arrow-up';
        this.items.sort((a, b) => {
          return b.number - a.number;
        })
      }
    },
    sortByString() {
      if (this.descStrings) {
        this.descStrings = false;
        this.stringsArrow = 'mdi-arrow-down';
        this.items.sort((a, b) => {
          if (a.string.toUpperCase() > b.string.toUpperCase()) return -1;
          if (a.string.toUpperCase() < b.string.toUpperCase()) return +1;
          return 0;
        })
      } else {
        this.descStrings = true;
        this.stringsArrow = 'mdi-arrow-up';
        this.items.sort((a, b) => {
          if (a.string.toUpperCase() > b.string.toUpperCase()) return +1;
          if (a.string.toUpperCase() < b.string.toUpperCase()) return -1;
          return 0;
        })
      }
    }
  }
}
</script>
